import Link from "next/link"
import { Button } from "@/components/ui/button"
import { ArrowRight, Award, CalendarCheck, Users } from "lucide-react"

const stats = [
  { label: "Happy Clients", value: "5000+", icon: Users },
  { label: "Success Rate", value: "98%", icon: Award },
  { label: "Years Experience", value: "10+", icon: CalendarCheck },
]

export default function Hero() {
  return (
    <section className="relative overflow-hidden bg-linear-to-br from-[#EAF7F1] via-white to-[#E7F0FF] py-24 px-4">
      <div className="pointer-events-none absolute -right-10 top-10 h-72 w-72 rounded-full bg-primary/20 blur-[120px]" />
      <div className="pointer-events-none absolute -left-20 bottom-0 h-112 w-md rounded-full bg-secondary/30 blur-[160px]" />

      <div className="relative z-10 mx-auto flex max-w-5xl flex-col items-center text-center">
        <p className="mb-4 text-sm font-semibold uppercase tracking-[0.35em] text-primary/80">
          Personalized Nutrition
        </p>
        <h1 className="text-5xl font-bold leading-tight text-foreground md:text-6xl">
          Dietify – Your Personal
          <br />
          <span className="text-primary">Online Dietician</span>
        </h1>
        <p className="mt-6 max-w-3xl text-lg leading-relaxed text-foreground/70 md:text-xl">
          Personalized diet plans, expert nutritional guidance, and disease-specific programs crafted just for you.
          Get meal plans that fit your lifestyle and show results fast.
        </p>

        <div className="mt-8 flex flex-col items-center gap-4 sm:flex-row">
          <Link href="/book" className="w-full sm:w-auto">
            <Button
              size="lg"
              className="w-full min-w-[220px] justify-center bg-primary text-primary-foreground hover:bg-primary/90"
            >
              Book Your Appointment
              <ArrowRight className="ml-2 h-5 w-5" />
            </Button>
          </Link>
          <Link href="/services" className="w-full sm:w-auto">
            <Button
              size="lg"
              variant="outline"
              className="w-full min-w-[220px] justify-center border-primary text-primary hover:bg-primary/5"
            >
              Explore Services
            </Button>
          </Link>
        </div>

        <div className="mt-16 grid w-full gap-6 md:grid-cols-3">
          {stats.map((stat) => (
            <div
              key={stat.label}
              className="rounded-3xl border border-white/80 bg-white/80 p-8 text-center shadow-[0_20px_60px_rgba(28,199,141,0.12)] backdrop-blur"
            >
              <div className="mx-auto mb-4 flex h-14 w-14 items-center justify-center rounded-2xl bg-primary/10 text-primary">
                <stat.icon className="h-7 w-7" />
              </div>
              <div className="text-4xl font-bold text-foreground">{stat.value}</div>
              <p className="mt-2 text-sm font-medium uppercase tracking-[0.35em] text-foreground/60">{stat.label}</p>
            </div>
          ))}
        </div>
      </div>
    </section>
  )
}
